import http from "../http-common";

class EmpDataService {
  getAll() {
    return http.get("/emp");
  }

  get(dno) {
    return http.get(`/emp/${dno}`);
  }

  create(data) {
    return http.post("/emp", data);
  }

  update(dno, data) {
    return http.put(`/emp/${dno}`, data);
  }

  delete(dno) {
    return http.delete(`/emp/deletion/${dno}`);
  }

  deleteAll() {
    return http.delete(`/emp`);
  }

  findByDname(ename) {
    return http.get(`/emp?ename=${ename}`);
  }
}

export default new EmpDataService();
